.page-wrapper {
    display: flex;
    min-height: 100vh;
    background-color: #f0f0f0; 
}

.register-form {
    text-align: center;
    margin-top: 50px;
    max-width: 450px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    flex-direction: column; 
    align-items: center; 
}

.register-form h1 {
    font-size: 50px;
    margin-bottom: 25px;
    color: #318bc6;
}

.divider {
    width: 100%;
    margin-top: 15px;
    margin-bottom: 10px;
    border: 0.75px solid #ddd;
}

.register-form form {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ffffff; 
    padding: 20px;
    border-radius: 10px;
    width: 100%; 
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.register-form input[type="text"],
.register-form input[type="password"],
.register-form input[type="date"],
.register-form select {
    width: 100%;
    padding: 12px;
    margin: 5px;
    border: 1px solid #ddd;
    border-radius: 5px;
    transition: all 0.3s ease;
}

.name-fields {
    display: flex;
    margin: 5px;
    width: 485px;
}

.register-form input[type="text"]:focus,
.register-form input[type="password"]:focus,
.register-form input[type="date"]:focus,
.register-form select:focus {
    outline: none;
    border-color: #318bc6;
    box-shadow: 0 0 5px rgba(24, 119, 242, 0.5);
}

.register-form label {
    text-align: left;
    width: 100%;
    margin-top: 2px;
    font-size: 14px;
    color: #999;
}

.register-form button[type="submit"] {
    background-color: #45b71f; 
    color: white;
    padding: 12px 24px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 20px;
    font-size: 18px;
    text-transform: uppercase;
    transition: background-color 0.3s ease;
}

.register-form button[type="submit"]:hover {
    background-color: #3b9a1b;
}

.register-form p {
    color: red;
    margin-top: 10px;
    font-size: 16px;
}

.register-form input[type="text"].error,
.register-form input[type="password"].error,
.register-form input[type="date"].error,
.register-form select.error {
    border-color: red;
}

.register-form .error-message {
    color: red;
    font-size: 14px;
    margin-top: 5px;
}

.register-form .general-error {
    color: red;
    font-size: 16px;
    margin-top: 10px;
}